@import 'mixins/conversions';

// Fonts
$font-family-light: 'opensans-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-family-book: 'opensans-semibold', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

$font-family-sans-serif: $font-family-light;
$font-family-monospace: 'SFMono-Regular', Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;

// Font Weights
$font-bold: 700;
$font-medium: 600;
$font-normal: 400;

// Font Sizes
$font-size-base: calc-rem(16px);
$font-size-lg:   calc-rem(21px);
$font-size-sm:   calc-rem(12px);

$font-size-large:  calc-rem(21px); // ~21px
$font-size-small:  calc-rem(12px); // ~12px
$font-size-btn:    calc-rem(13px); // ~13px

$showTextShadows: false;

// ========== 2020 BRANDING REDESIGN ==========
// ========== Colors ==========
// CORPORATE COLORS
$cl-red:              #e10600;
$cl-red-dark:         #b11e2b;
$cl-orange:           #f7941d;
$cl-orange-dark:      #f15a29;
$cl-blue-adj:         #257bb1;
$cl-blue:             $cl-blue-adj;
$cl-blue-dark:        #0d395d;
$cl-green:            $cl-blue;
$cl-green-dark:       #007987;
// NEUTRAL COLORS
$cl-gray-dark:        #5a5a5a;
$cl-gray-medium:      #828282;
$cl-gray-light:       #aaaaaa;
$cl-gray-white:       #e6e6e6;
$cl-blackest:        #000000;
$cl-white:           #ffffff;
// This color is old but the new brand doesn't have an equivalent so we need to find it & remove it
$gallery:            #ebebeb;
// ADDITIONS/DEVIATIONS FOR ACCESSIBILITY
$cl-copy:            #404040;
$cl-green-success:   #34836e;
$cl-gray-white-rgba:  rgba(230,230,230,0.5);
$cl-gray-white-light: #efefef;
$cl-link-disabled-dark:    #c6c6c6;
$cl-link-disabled-light:    #767676;
$cl-disabled-bg:     $cl-gray-white;
$cl-disabled-copy:   $cl-gray-medium;
$cl-transparent:     rgba(0,0,0,0);
// OG BRAND COLORS BEFORE WE CHANGED THEM
$cl-blue-OG:          #3899d5; // Passing alternative #257BB1
$cl-green-OG:            #48b295; // Passing alternative #35826E
// ALERT COLORS
$cl-success:         $cl-green-success;
$cl-warning:         $cl-orange;
$cl-info:            $cl-blue-dark;
$cl-danger:          $cl-red-dark;

$cl-default:         $cl-gray-white;
$cl-dark:            $cl-copy;
$cl-light:           $cl-gray-white;
$cl-btn-dark:        $cl-blue-dark;

// ========== COLOR VARIABLE MAPPING ==========
$black:             $cl-blackest;       // #000000  // #3a3a3a;
$clBlack:           $cl-copy;           // #3a3a3a
$cl-black:          $cl-copy;           // #3a3a3a
$white:             $cl-white;
$transparent:       $cl-transparent;    // rgba(0, 0, 0, 0);
// Theme Colors
$valencia:          $cl-red;            // #d23e43; // RED
$cerulean:          $cl-blue;           // #007faa; // BLUE
$shamrock:          $cl-blue;          // #1e824c; // GREEN
$amethyst:          $cl-orange;         // #77448b; // PURPLE
// Grays
//$gallery:           #ebebeb;
$seashell:          $cl-gray-white;     // #f1f1f1;
$alto:              $cl-gray-light;     // #d2d2d2;
$gray:              $cl-gray-medium;    // #767676;
$dark-gray:         $cl-gray-dark;      // #494949;
// Color Messages
$success:           $cl-success;        // #428542;
$warning:           $cl-warning;        // lighten(#dfaf13, 0.5); // darken(#ffd761, 0.1); // #dfaf13;
$info:              $cl-info;           // #006fe6; // #4da3ff; // #285cab;
$danger:            $cl-danger;         // #b33d4b;
// BS Overrides
$primary:           $cl-blue-adj;
$secondary:         $cl-default;         // lighten($alto, 8%);
$light:             $cl-light;
$dark:              $cl-dark;
$link:              $cl-blue;


// ========== Gradients ==========
// GRADIENT COLORS, RGB
// COLOR - Left-to-Right, Vice Versa
$cl-grad-db-b: linear-gradient(to right, #0d395d, #194f7a, #256797);
$cl-grad-dr-r: linear-gradient(to right, #9e0400, #b0150d, #c2221a);
$cl-grad-do-o: linear-gradient(to right, #f15a29, #f46925, #f57821);
$cl-grad-dg-g: linear-gradient(to right, #007987, #03878e, #199692);
$cl-grad-b-db: linear-gradient(to left, #0d395d, #194f7a, #256797);
$cl-grad-r-dr: linear-gradient(to left, #9e0400, #b0150d, #c2221a);
$cl-grad-o-do: linear-gradient(to left, #f15a29, #f46925, #f57821);
$cl-grad-g-dg: linear-gradient(to left, #007987, #03878e, #199692);

// BLACK & WHITE - Up & Down, Vice Versa
$cl-grad-dg-gw: linear-gradient(to top, #404040, #464646, #4d4d4d, #535353, #5a5a5a);
$cl-grad-gw-dg: linear-gradient(to bottom, #5a5a5a, #535353, #4d4d4d, #464646, #404040);
$cl-grad-gl-gw: linear-gradient(to bottom, #e6e6e6, #d7d7d7, #c8c8c8, #b9b9b9, #aaaaaa);


// GRADIENT COLORS, RGBA
// COLORS - Left-to-Right, Vice Versa
$cl-grad-rgb-db-b: linear-gradient(to right, rgba(13, 57, 93, 1), rgba(25, 79, 122, 1), rgba(37, 103, 151, 1));
$cl-grad-rgb-dr-r: linear-gradient(to right, rgba(158, 4, 0, 1), rgba(176, 21, 13, 1), rgba(194, 34, 26, 1));
$cl-grad-rgb-do-o: linear-gradient(to right, rgba(241, 90, 41, 1), rgba(244, 105, 37, 1), rgba(245, 120, 33, 1));
$cl-grad-rgb-dg-g: linear-gradient(to right, rgba(0, 121, 135, 1), rgba(3, 135, 142, 1), rgba(25, 150, 146, 1));
$cl-grad-rgb-b-db: linear-gradient(to left, rgba(13, 57, 93, 1), rgba(25, 79, 122, 1), rgba(37, 103, 151, 1));
$cl-grad-rgb-r-dr: linear-gradient(to left, rgba(158, 4, 0, 1), rgba(176, 21, 13, 1), rgba(194, 34, 26, 1));
$cl-grad-rgb-o-do: linear-gradient(to left, rgba(241, 90, 41, 1), rgba(244, 105, 37, 1), rgba(245, 120, 33, 1));
$cl-grad-rgb-g-dg: linear-gradient(to left, rgba(0, 121, 135, 1), rgba(3, 135, 142, 1), rgba(25, 150, 146, 1));

// BLACK * & WHITE - Up & Down, Vice Versa
$cl-grad-rgb-dg-gw-bt: linear-gradient(to top,rgba(64,64,64,1),rgba(70,70,70,1),rgba(77,77,77,1),rgba(83,83,83,1),rgba(90,90,90,1));
$cl-grad-rgb-dg-gw-tb: linear-gradient(to bottom,rgba(64,64,64,1),rgba(70,70,70,1),rgba(77,77,77,1),rgba(83,83,83,1),rgba(90,90,90,1));
$cl-grad-rgb-db-b-bt: linear-gradient(to top, rgba(13, 57, 93, 1), rgba(25, 79, 122, 1), rgba(37, 103, 151, 1));
$cl-grad-rgb-db-b-tb: linear-gradient(to bottom, rgba(13, 57, 93, 1), rgba(25, 79, 122, 1), rgba(37, 103, 151, 1));


// Robbie's Gradient Implementation
$brand-colors: (
    cerulean: (
        color: $cerulean,
        gradient-start-color: $cerulean,
        gradient-end-color: #00E9FF,
    ),
    valencia: (
        color: $valencia,
        gradient-start-color: $valencia,
        gradient-end-color: #E9698A,
    ),
    shamrock: (
        color: $shamrock,
        gradient-start-color: $shamrock,
        gradient-end-color: #46EB9C,
    ),
    amethyst: (
        color: $amethyst,
        gradient-start-color: $amethyst,
        gradient-end-color: #D368FF,
    )
);


$buttonColors: (
    'primary': $cl-blue-adj,
    'secondary': $cl-default,
    'dark': $cl-blue-dark,
    'light': $cl-gray-white,
    'white': $cl-white,
    'inverse': $cl-copy,
    'link': $link,
    'black': $cl-blackest
);

$gradientColor: (
    'gray': '',
    'valencia': $cl-grad-dr-r,
    'cerulean': $cl-grad-db-b,
    'shamrock': $cl-grad-dg-g,
    'amethyst': $cl-grad-do-o,
	'red': $cl-grad-dr-r,
	'blue': $cl-grad-db-b,
	'green': $cl-grad-db-b,
	'orange': $cl-grad-do-o
);

$bgGradientColor: (
    'red': $cl-grad-dr-r,
    'blue': $cl-grad-db-b,
    'green': $cl-grad-db-b,
    'orange': $cl-grad-do-o
);

$new-brand-colors19: (
	'red': $cl-red,
	'red-dark': $cl-red-dark,
	'blue': $cl-blue,
	'blue-dark': $cl-blue-dark,
	'green': $cl-blue,
	'green-dark': $cl-blue-dark,
	'orange': $cl-orange,
	'orange-dark': $cl-orange-dark,
	'gray-dark': $cl-gray-dark,
	'gray-medium': $cl-gray-medium,
	'gray-light': $cl-gray-light,
	'gray-white': $cl-gray-white
);

$action-colors: (
    'default': $cl-default,
    'primary': $primary,
    'dark': $dark
);

$gray-colors: (
    'gallery': $gallery,
    'seashell': $seashell,
    'alto': $alto,
    'gray': $gray,
    'dark-gray': $dark-gray
);

$message-colors: (
    'default': $cl-default,
    'success': $success,
    'warning': $warning,
    'info': $info,
    'danger': $danger
);

$all-colors: (
    'primary': $primary,
    'secondary': $cl-default,
    'default': $cl-default,
    'success': $success,
    'danger': $danger,
    'warning': $warning,
    'info': $info,
    'light': $light,
    'dark': $dark,
    'inverse': $dark,
    'link': $link,
    'white': $white,
    'black': $black
);

$theme-colors: (
    'valencia': $valencia,
    'cerulean': $cerulean,
    'shamrock': $shamrock,
    'amethyst': $amethyst
);

$line-height-base:        1.625; // 26/16

//** Background color for `<body>`.
$body-bg:               $white;

// Links
//
// Style anchor elements.
$link-color:            $cerulean;
$link-decoration:       none;
$link-hover-color:      darken($link-color, 15%);
$link-hover-decoration: none;

// Brand
$brand-primary:         $primary;
$brand-success:         $success;
$brand-info:            $info;
$brand-warning:         $warning;
$brand-danger:          $danger;

// Spacing
$letter-spacing:        -0.015em;

// Components
$border-radius:         0.25rem;
$drop-shadow:           0 0 5px 0 rgba($black, 0.35);

// Autocomplete
$ac-bg:                 $white;
$ac-text:               $dark-gray;
$ac-item-hover-bg:      $alto;
$ac-highlight-bg:       $gallery;

// Forms
$icon-input-text:      $dark-gray;

$nb-mobile-height:      60px;
$nb-mobile-logo-width:  60px;

// Tables
$intColumnMax:          12;

$grid-breakpoints: (
    // Extra small screen / phone
    xs: 0,
    // Small screen / phone
    sm: 576px,
    // Medium screen / tablet
    md: 768px,
    // Large screen / desktop
    lg: 992px,
    // Extra large screen / wide desktop
    xl: 1200px
);

// Forms
$input-border-focus:             $cerulean;

// Form validation
$form-feedback-valid-color:   $success;
$form-feedback-invalid-color: $danger;

// Borders
$border-radius-base: 4px;
$input-border-radius: $border-radius-base;


@function encodecolor($string) {
    @if type-of($string) == 'color' {
        $hex: str-slice(ie-hex-str($string), 4);
        $string: unquote("#{$hex}");
    }
    $string: '%23' + $string;
    @return $string;
}
