@import '../variables';
@import '../mixins/mixins';
@import '../themes';

ngb-datepicker {
    &.dropdown-menu {
        transform: translate(0px, 38px) !important;
    }
    .custom-day {
        text-align: center;
        padding: 0.185rem 0.25rem;
        display: inline-block;
        width: 40px;
        line-height: 2.2;
    }
    .custom-day.range, .custom-day:hover {
        font-family: $font-family-book;
        height: 36px;
    }
    .custom-day.faded {
        font-family: $font-family-light;
        height: 36px;
    }
    [ngbDatepickerDayView] {
        text-align: center;
        width: 40px;
        height: 36px;
        line-height: 2.2rem;
        border-radius: .25rem;
        background: 0 0;
    }
    &:before, &:after {
        content: '';
        display: inline-block;
        position: absolute;
        left: 26px;
    }
    &:before {
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        top: -12px; //
    }
    &:after {
        top: -12px;
    }

    border: none !important;
    border-radius: 0 !important;
    display: flex;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
    [ngbDatepickerDayView] {
        border-radius: 0 !important;
    }
    .ngb-dp-day {
        outline: 0 !important;
    }
    .btn {
        box-shadow: none !important;
        text-transform: uppercase !important;
    }
    .ngb-dp-day, .ngb-dp-week-number, .ngb-dp-weekday {
        width: 40px;
        height: 36px;
        //margin: -1px -5px !important;
        font-size: 14px !important;
    }
    .ngb-dp-header {
        align-items: center;
        border-bottom: 0;
        border-radius: 0 !important;
        padding-top: 0 !important;
        height: 57px;
        text-transform: uppercase !important;
    }

    .ngb-dp-month-name {
        font-family: $font-family-book;
        font-size: 16px;
        font-weight: 800;
        line-height: 34px;
    }

    .ngb-dp-arrow-btn {
        margin: 0;
        width: 57px;
        height: 57px;
        display: flex;
	    border-radius: 0;
        &:focus {
            outline-width: 0;
        }
        &:first-child {
            .ngb-dp-navigation-chevron {
                margin: 0 1.1rem;
            }
        }
    }
    .btn-light {
        &:hover, &:active, &:focus {
            font-family: $font-family-book;
            outline: 0;
        }
    }
    [ngbDatepickerDayView].outside {
        opacity: inherit !important;
    }
    .btn-light:focus {
        box-shadow: none !important;
    }

    .btn-light:not([disabled]):not(.disabled):active, .btn-light:not([disabled]):not(.disabled).active, .show > .btn-light.dropdown-toggle {
        box-shadow: none;
        outline: 0;
        font-family: $font-family-book;
    }

    .bg-primary {
        &:hover, &:active, &:focus {
            outline: 0;
        }
    }
    .ngb-dp-navigation-select {
        .custom-select {
            margin-right: 4px;
            margin-left: 4px;
        }
    }

    .ngb-dp-arrow {
        height: inherit;
        &.right {
            .ngb-dp-arrow-btn {
                // border-left: 1px solid rgba(255, 255, 255, 0.15);
                .ngb-dp-navigation-chevron {
                    margin: 0 0.9rem;
                }
            }
        }
    }

    .ngb-dp-week {
        padding: 0 !important;
    }
    .ngb-dp-month {
        margin: 7px;
    }

    .ngb-dp-weekday {
        font-family: $font-family-book;
        text-transform: uppercase;
        font-size: 14px;
        font-style: inherit;
        font-weight: 800;
    }

    .ngb-dp-weekdays {
        border-bottom: none !important;

        &.bg-light {
            background-color: $white !important;
        }
    }

    .ranges {
        background-color: $gallery;
        border-top: 1px solid $alto;
        display: flex;
        float: none;
        height: 50px;
        margin: 0;
        width: 100%;
        justify-content: flex-end;

        .range_inputs {
            border-left: 1px solid $alto;
            display: flex;
            flex-direction: row-reverse;
            width: 315px;
            .applyBtn {
                flex: 1;
                width: 100%;
                justify-content: center;
	            border-radius: 0;
            }
        }
    }
    &[ng-reflect-display-months="2"] {
        .ngb-dp-month {
            .ngb-dp-month-name {
                color: $cl-black;
                line-height: 40px !important;
                &.bg-light {
                    background-color: transparent !important;
                }
            }
        }
    }
    @include themify($themes) {
        .custom-day.range, .custom-day:hover {
            background-color: themed-component('day-range-background-color', 'ngbs-datepicker');
            color: themed-component('day-range-color', 'ngbs-datepicker');
        }
        .custom-day.faded {
            background-color: themed-component('day-faded-background-color', 'ngbs-datepicker');
            color: themed-component('day-faded-color', 'ngbs-datepicker');
        }
        &:before {
            border-bottom: themed-component('arrow-border-bottom', 'ngbs-datepicker');
        }
        .ngb-dp-header {
            background-color: themed-component('header-background-color', 'ngbs-datepicker') !important;
        }
        .ngb-dp-month-name {
            color: themed-component('month-name-color', 'ngbs-datepicker');
        }
        &:first-child {
            border-right: themed-component('month-arrow-border-right', 'ngbs-datepicker');
        }
        .btn-light {
	        &:not([disabled]):not(.disabled) {
		        transition-property: none;
		        &:hover, &:active, &:focus {
			        /*background-color: $cl-gray-dark;
			        border-color: rgba(255, 255, 255, 0.15);*/
			        background-color: themed-component('date-picker-table-td-available-background-color', 'datetime');
			        color: themed-component('date-picker-table-td-color', 'datetime');
		        }
	        }
	        &.text-muted {
		        &:hover, &:active, &:focus {
			        color: themed-component('date-picker-table-td-color', 'datetime') !important;
		        }
	        }
        }

        .btn-link {
	         background-color: themed-component('btn-light-background-color', 'ngbs-datepicker');
	         color: themed-component('btn-light-color', 'ngbs-datepicker') !important;
	        background-image: none;
	        border-right: themed-component('date-picker-table-available-border', 'datetime');
	        &:hover, &:active, &:focus {
		        background-color: themed-component('btn-light-background-color', 'ngbs-datepicker');
		        color: themed-component('btn-light-color', 'ngbs-datepicker') !important;
		        border-color: rgba(255, 255, 255, 0.15);
	        }
	        &:not([disabled]):not(.disabled) {
		        &:hover, &:active, &:focus {
			        background-color: $cl-gray-dark;
					border-color: rgba(255, 255, 255, 0.15);
		        }
	        }
        }
	    .right {
		    .btn-link {
			    border-left: themed-component('date-picker-table-available-border', 'datetime');
			    border-right: 0;
		    }
	    }
        .text-muted {
            // color: themed-component('text-muted-color', 'ngbs-datepicker');
        }
        .bg-primary {
	        background-color: themed-component('date-picker-table-td-active-background-color', 'datetime') !important;
	        color: themed-component('date-picker-table-td-color', 'datetime');
            &:hover, &:active, &:focus {
                background-color: themed-component('bg-primary-background-color', 'ngbs-datepicker');
                color: themed-component('bg-primary-hover-color', 'ngbs-datepicker');
            }
        }
	    .ngb-dp-today {
		    .btn-light {
			    &:not([disabled]):not(.disabled) {
				    background-color: $white;
				    color: $black;
				    font-family: $font-family-light;
				    &:hover, &:active, &:focus {
					    background-color: themed-component('bg-primary-background-color', 'ngbs-datepicker');
					    color: themed-component('bg-primary-hover-color', 'ngbs-datepicker');
				    }
			    }
		    }
		    .bg-primary {
			    background-color: themed-component('date-picker-table-td-active-background-color', 'datetime') !important;
			    color: themed-component('date-picker-table-td-color', 'datetime');
			    &:hover, &:active, &:focus {
				    background-color: themed-component('bg-primary-background-color', 'ngbs-datepicker');
				    color: themed-component('bg-primary-hover-color', 'ngbs-datepicker');
			    }
		    }
	    }
        .ngb-dp-weekday {
            background-color: themed-component('weekday-background-color', 'ngbs-datepicker');
            color: themed-component('weekday-color', 'ngbs-datepicker');
        }
        .ranges, .range_inputs {
            background-color: themed-component('ranges-background-color', 'ngbs-datepicker');
            border-left: themed-component('range-inputs-border-left', 'ngbs-datepicker');
        }
    }
}

.datepicker-range {
    .ngb-dp-month {
        .ngb-dp-month-name {
            color: $cl-black;
            line-height: 40px !important;
            &.bg-light {
                background-color: transparent !important;
            }
        }
    }
}
